

















































// import Vue from "vue";
// import Component from "vue-class-component";
import { PostModel, RequestData } from "@/x-vue/services/interfaces";
import { AppService } from "@/service/app.service";
import PostTitleMeta from "@/x-vue/components/forum/PostTitleMeta.vue";
import store from "@/store";
import { Vue, Component, Watch } from "vue-property-decorator";
@Component({
  components: {
    PostTitleMeta,
  },
})
export default class PostList extends Vue {
  app = AppService.instance;

  loadingPosts = false;

  total = 0;
  limit = 5;
  noOfPages = 10;
  currentPage = "1";

  options: RequestData = {};

  get category(): string {
    return this.$route.params.category;
  }

  get subCategory(): string {
    return this.$route.query.sc as string;
  }

  get posts(): PostModel[] {
    return store.state.posts;
  }

  get createLink(): string {
    let link = "/edit/" + this.category;
    //check if subcategory exist in query
    if (this.subCategory) {
      link += "?sc=" + this.subCategory;
    }
    return link;
  }

  linkGen(pageNum: number): string {
    // return pageNum === 1 ? "?" : `?page=${pageNum}`;
    const link: string[] = [];
    if (pageNum > 1) link.push(`page=${pageNum}`);
    if (this.subCategory) {
      link.push("sc=" + this.subCategory);
    }
    return "?" + link.join("&");
  }

  onPageChanged(page: number): void {
    // console.log("page changed", page);
    this.options.page = page;
    this.loadPosts();
  }

  async initPostList(): Promise<void> {
    store.state.posts = [];
    this.options.categoryId = this.$route.params.category;
    this.options.userIdx = this.$route.params.userIdx;
    this.options.limit = this.limit;
    this.options.comments = 1;
    if (this.$route.query.page) {
      this.currentPage = this.$route.query.page as string;
      this.options.page = this.currentPage;
    } else {
      this.currentPage = "1";
      this.options.page = 1;
    }

    if (this.subCategory) {
      this.options.subcategory = this.subCategory;
    }

    try {
      this.total = await this.app.api.postCount(this.options);
      this.noOfPages = Math.ceil(this.total / this.limit);
    } catch (e) {
      this.app.error(e);
    }
  }

  async mounted(): Promise<void> {
    // console.log("PostList::mounted();params", this.$route.params);
    this.initPostList();
    this.loadPosts();
  }

  @Watch("$route.query.sc")
  onQueryChange(subcategory: string, oldsubcategory: string): void {
    // return on the first load. oldsubcategory is undefine on the first load
    console.log(subcategory, oldsubcategory);
    // if (oldsubcategory == undefined) {
    //   return;
    // }
    this.initPostList();
    this.loadPosts();
  }

  async loadPosts(): Promise<void> {
    // console.log(this.options);
    this.loadingPosts = true;
    try {
      store.state.posts = await this.app.api.postSearch(this.options);
      // console.log(store.state.posts);
      this.loadingPosts = false;
    } catch (error) {
      this.app.error(error);
      this.loadingPosts = false;
    }
  }
}
